  width: 24px;
  height: 3px;
  left: 6px;
  border-radius: 2px;
  background-color: #cccccc;
  content: "";
  transition: all 0.3s ease;
  transform-origin: center left;
}
.header__btn::after {
  top: 6px;
}
.header__btn::before {
  bottom: 6px;
}
.is-open .header__btn::after {
  top: 5px;
